<template>
    <div class="container card text-center" align="center">
        <div class="row text-center pt-4" align="center">
            <b-tabs card class="col-lg-12">
              {{ detalle }}
                <b-tab v-for="i in tabs" :key="'dyn-tab-' + i" :title="i">
                    <div>
                        <div class="container">
                            <div class="row">
                                <div class="col-sm">
                                    <div>
                                        <b-table
                                            selectable
                                            selectedVariant="success"
                                            select-mode="single"
                                            responsive="sm"
                                            striped hover :small="true" 
                                            :items="item"
                                            @row-dblclicked="rowSelected"
                                            ></b-table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <b-button size="sm" variant="danger" class="float-right" @click="closeTab(i)">
                      Close tab
                    </b-button>
                </b-tab>

                <!-- New Tab Button (Using tabs slot) -->
                <template slot="tabs">
                <b-nav-item @click.prevent="newTab" href="#"><b>+</b></b-nav-item>
                </template>

                <!-- Render this if no tabs -->
                <div slot="empty" class="text-center text-muted">
                There are no open tabs<br>
                Open a new tab using the <b>+</b> button above.
                </div>
            </b-tabs>
        </div>
    </div>
    
</template>
<script>
// import detalle from '../components/CostoPromedio/DetalleCategorias';
const axios = require('axios');
const moment = require("moment")
var _ = require('lodash');
export default {
  props: ['selectedCategory', 'fechaFin', 'fechaInicio'],
  components: {
    // detalle
  },
  data () {
    return {
      detalle: '',
      item: [],
      selected: [],
      tabs: [this.selectedCategory.nombre],
      totalGastado: 0,
      totalCobrado: 0,
      comensales: 0
    }
  },
  mounted () {
      // console.log("this.selectedCategory", this.selectedCategory)
      switch (this.selectedCategory.tipo) {
        case "Comensales contrato":
          this.detalleComensalesComensales(this.selectedCategory.tipo);
          break;
        case "Cargos":
          this.detalleComensalesCargo();
          break;
        case "Cargos facturados":
          this.detalleComensalesCargosFacturados();
          break;
        case "Eventos recaudacin":
          this.numeroEventosRecaudacion();
          break;
        case "Contratos":
          this.detalleComensalesComensales();
          break;
        case "Venta de vinos":
          this.ventaVinos();
          break;
        case "Gasto insumo":
          this.gatoInsumo();
          break;
        case "Gasto activo":
          this.gatoActivo();
          break;
        case "Gasto operativo":
          this.gastoOperativo();
          break;
        case "Gasto personal":
          this.gastoPersonal();
          break;
        case "BODEGA INSUMOS":
          this.detalleGastoInsumoCategoria();
          break;
        case "CERVEZA":
          this.detalleGastoInsumoCategoria();
          break;
        case "COCINA INSUMOS":
          this.detalleGastoInsumoCategoria();
          break;
        case "HIELO":
          this.detalleGastoInsumoCategoria();
          break;
        case "QUIMICOS":
          this.detalleGastoInsumoCategoria();
          break;
        case "REFRESCOS":
          this.detalleGastoInsumoCategoria();
          break;
        case "FAMILIARES":
           this.detalleGastoPersonalCategoria()
           break;
        case "PAGOS":
           this.detalleGastoPersonalCategoria()
           break;
        case "COMBUSTIBLES":
           this.detalleGastoPersonalSubcategoria()
           break;
        case "GASTOS ADMINISTRACION":
           this.detalleGastoPersonalSubcategoria ()
           break;
        case "GASTOS EVENTOS":
           this.detalleGastoPersonalSubcategoria ()
           break;
        case "GASTOS MANTENIMIENTO":
           this.detalleGastoPersonalSubcategoria ()
           break;
        case "NOMINA":
           this.detalleGastoPersonalSubcategoria ()
           break;
        case "PAGOS EXTRAS":
           this.detalleGastoPersonalSubcategoria ()
           break;
        case "PREMIO DE LEALTAD":
           this.detalleGastoPersonalSubcategoria ()
           break;
        case "PUBLICIDAD":
           this.detalleGastoPersonalSubcategoria ()
           break;
        case "SERVICIOS":
           this.detalleGastoPersonalSubcategoria ()
           break;
        case "GASTOS COCINA":
           this.detalleGastoPersonalSubcategoria ()
           break;
        default:
          break;
      }
  },
  methods: {
    async detalleComensalesComensales () {
      await this.getData('detalleComensalesContrato').then((data) => {
          _.forEach(data, (val, key) => {
            this.item.push({tipo: key, total: val})
          })
      })
    },
    async detalleComensalesCargo () {
      await this.getData('detalleComensalesCargo').then((data) => {
           _.forEach(data, (val, key) => {
            this.item.push({tipo: key, total: val})
          })
      })
    },
    async detalleComensalesCargosFacturados () {
      await this.getData('detalleComensalesCargosFacturados').then((data) => {
        this.data.push({nombre: 'Premio de lealtad', total: _.floor(data, 4)});
        this.totalGastado += parseFloat(data);
      })
    },
    async detalleTotalCobradoContratos () {
      await this.getData('detalleCobradoContratos').then((data) => {
        let x = 0;
        _.forEach(data, (val) => {
          x+= val.total;
          this.item.push({Contrato: val.contrato, saldo_inicial: val.saldo_inicial, cargos: val.cargos, cargos_facturados: val.cargos_Facturados, devolucion: val.devolucion, total: val.total})
        })
        this.detalle = 'Total: ' + x
      })
    },
    async detalleGastoInsumoCategoria () {
      await this.getData('detalleGastoInsumo', this.selectedCategory.id_categoria).then((data) => {
        let x = 0;
        _.forEach(data, (val) => {
          x+= parseFloat(val.totalInumos);
          this.item.push({producto: val.producto, descripcion: val.descripcion, tipo: val.tipo, cantidad: val.cantidad, precio_adquisicion: val.precio_adquisicion, precio_venta: val.precio_venta, importe: val.importe, total: val.totalInumos})
        })
        this.detalle = 'Total: ' + x
      })
    },
    async detalleGastoPersonalCategoria () {
      await this.getData('detalleGastoPersonal', this.selectedCategory.id_subcategoria, this.selectedCategory.gasto).then((data) => {
        let x = 0;
         _.forEach(data, (val) => {
          x+= parseFloat(val.importe);
          this.item.push({producto: val.nombre, descripcion: val.descripcion, fecha: val.fecha, facturado: val.facturado, importe: val.importe})
        })
        this.detalle = 'Total: ' + x
      })
    },
    async detalleGastoPersonalSubcategoria () {
      await this.getData('detalleGastoPersonalSubcategoria', this.selectedCategory.tipo, this.selectedCategory.gasto).then((data) => {
        let x = 0;
         _.forEach(data, (val) => {
          x+= parseFloat(val.total);
          this.item.push({producto: val.producto, descripcion: val.descripcion, cantidad: val.cantidad, facturado: val.facturado, precio_adquisicion: val.precio_adquisicion, total: val.total})
        })
        this.detalle = 'Total: ' + x
      })
    },
    async getData (opcion, detalle, detalle2) {
      return await new Promise((resolve, reject) => {
        try {
          let payload = {
            fecha1: moment(this.fechaInicio).format('YYYY-MM-DD'),
            fecha2: moment(this.fechaFin).format('YYYY-MM-DD'),
            opcion: opcion,
            detalle: detalle,
            detalle2: detalle2
          }
          let personForm = this.toFormData(payload);
          axios
            .post('https://villaconin.mx/reportes/costoPromedio.php', personForm)
            .then(response => {
                // eslint-disable-next-line no-console
                if (_.isEmpty(response)) {
                  resolve(0)
                } else {
                  resolve(response.data)
                }
            })
            .catch(error => {
              this.errored = true
              // eslint-disable-next-line no-console
              console.log(error)
            })
            .finally(() => this.loading = false)
        }
        catch (error) {
          reject(error)
        }
      })
    },
    toFormData(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
      }
      return formData;
    },
    formatPrice(value) {
      let val = (value/1).toFixed(2)
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    },
    rowSelected(items) {
      this.selected = items;
      // eslint-disable-next-line no-console
      // console.log('SELECCION', items);
      let nombre = '';
      switch (items.tipo) {
          case 'Contrato':
              nombre = 'Comensales contrato';
              break;
          case 'Contratos':
              nombre = 'Total Cobrado contratos';
              break;
          default:
            nombre = items.nombre
              break;
      }
      this.tabs.push(String(nombre));
    },
    closeTab(x) {
      for (let i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i] === x) {
          this.tabs.splice(i, 1)
        }
      }
    }
  },
  created () {
      // eslint-disable-next-line no-console
      // console.log('selectedCategory', this.selectedCategory)
  }
}
</script>
